.hero_ctn {
	position: relative;
	display: flex;
	width: 100svw;
	height: 100svh;
}

.slogan_ctn {
	position: absolute;
	z-index: 1;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.left {
	font-size: 4em;
	text-transform: uppercase;
	font-weight: bolder;
	font-family: "Anton";
	width: 100%;
	height: 80%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.left #phil {
	margin-bottom: -25px;
	font-size: 3.62em;
	text-shadow: 2px 2px 2px pink;
}

.left #plays {
	font-family: "NCL Astonic";
	font-size: 1em;
	margin-top: -4.5em;
	color: var(--clr-cta-primary);
	text-shadow: 5px 5px 5px rgb(0, 44, 40);
	letter-spacing: 5px;
}

.left #bass {
	margin-top: -100px;
	font-size: 3.17em;
	text-shadow: 2px 2px 2px pink;
}

.right {
	height: 100%;
	width: 100%;
	display: flex;
	min-height: 674px;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 1em;

	.container {
		display: flex;
		flex-direction: column;
		gap: 1em;
		min-height: 674px;
		align-items: center;
		justify-content: center;
	}

	.buttons_ctn {
		display: flex;
		gap: 1em;
	}
}

.links_ctn {
	display: none;
	font-family: "Anton";
}

@media (min-width: 900px) {
	.slogan_ctn {
		flex-direction: row;
	}

	.left {
		font-size: 5em;
		justify-content: flex-start;
	}

	.left #phil {
	}

	.left #plays {
		font-size: 1.5em;
		margin-top: -3.5em;
		letter-spacing: -5px;
	}

	.left #bass {
		letter-spacing: -5px;
		margin-top: -0.5em;
	}

	.right {
		.container {
			min-width: 350px;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			gap: 1em;
			max-height: 200px;

			.links_ctn {
				display: inline-block;
				text-align: start;
				width: 100%;
				min-height: 250px;
			}

			.links_ctn > ul {
				display: flex;
				flex-direction: column;
			}

			.link_item {
				text-transform: uppercase;
				font-size: 3.5em;
				transition: 350ms ease-in-out all;
				letter-spacing: -2px;
				text-shadow: 1px 1px 2px pink;
			}

			.link_item:hover {
				font-size: 4em;
				transition: 350ms ease-in-out all;
				color: var(--clr-cta-primary);
				text-shadow: -1px 3px 1px white, -2px 3px 1px white, -3px 3px 1px white;
			}
		}
	}
}
